<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="gethouse"></el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-table :data="house" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="title" label="" width="120">
            </el-table-column>
            <el-table-column prop="acreage" label="" width="100">
            </el-table-column>
            <el-table-column prop="houseOwnerPhone" label="" width="150">
            </el-table-column>
            <el-table-column prop="status" label="" width="130" :formatter="formatStatus" sortable>
            </el-table-column>
            <el-table-column prop="address" label="" min-width="150" >

            </el-table-column>
            <el-table-column label="" width="150">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
            </el-pagination>
        </el-col>

        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form ref="hProperty" :model="hProperty" :rules="houseFormRules" label-width="100px" style="margin:20px ;width:60%; min-width:600px;" >
                    <el-form-item label="" prop="title">
                        <el-input v-model="hProperty.title"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="name">
                        <el-input v-model="hProperty.name"></el-input>
                    </el-form-item>

                <!--     <el-form-item label="">
                        <el-input v-model="hProperty.pic"></el-input>
                    </el-form-item> -->

                    <el-form-item label="" prop="totalPrice">
                        <el-input v-model="hProperty.totalPrice"></el-input>
                    </el-form-item>
                    <el-form-item label="(M^2/)" prop="refPrice">
                        <el-input v-model="hProperty.refPrice"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="area">
                        <el-input v-model="hProperty.area"></el-input>
                    </el-form-item>
                    </el-form-item>
                    <el-form-item label="" prop="tradingRight">
                        <el-input v-model="hProperty.tradingRight"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="houseType">
                        <el-input v-model="hProperty.houseType"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="houseOrientation">
                        <el-input v-model="hProperty.houseOrientation"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="floor">
                        <el-input v-model="hProperty.floor"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="decorate">
                        <el-input v-model="hProperty.decorate"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="equityYear">
                        <el-input v-model="hProperty.equityYear"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="buildYear">
                        <el-input v-model="hProperty.buildYear"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="addr">
                        <el-input v-model="hProperty.addr"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="phone">
                        <el-input v-model="hProperty.phone"></el-input>
                    </el-form-item>

                    <el-form-item label="">
                        <el-select v-model="hProperty.status" placeholder="">
                            <el-option label="" value="0"></el-option>
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                            <el-option label="" value="3"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="">
                        <el-radio-group v-model="hProperty.isPublic">
                            <el-radio :label="1" ></el-radio>
                            <el-radio :label="0" ></el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="">
                    <div class="upload-block">
                        <div class="source">
                            <el-upload
                              action="http://up-z1.qiniu.com"
                              list-type="picture-card"
                              :on-preview="handlePictureCardPreview"
                              :on-remove="handleRemove"
                              :on-success="uploadSuccess"
                              :data="postData">
                              <i class="el-icon-plus"></i>
                        </el-upload>
                        </div>
                        <el-dialog v-model="dialogVisible" size="tiny">
                            <img width="100%" :src="dialogImageUrl" alt="">
                        </el-dialog>
                    </div>

                    </el-form-item>
                    <el-form-item label="">
                        <el-input type="textarea" v-model="hProperty.desc"></el-input>
                    </el-form-item>
                <el-form-item>
                    <el-button @click.native="editFormVisible = false"></el-button>
                    <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer"></div>
        </el-dialog>
        </section>
</template>

<script>
    import util from '../../common/js/util'
    import constants from '../../common/js/constants'
    //import NProgress from 'nprogress'
    import {getQiniuToken, getDomain, gethouseListPage, deletehouse, edithouse} from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                house: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels:[],
                qiniuDomain: "",
                qiniuToken: "",

                editFormVisible: false,//
                editLoading: false,
                houseFormRules: {
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    area: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    totalPrice: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    refPrice: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    tradingRight: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    houseType: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    houseOrientation: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    tradingRight: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    floor: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    decorate: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    equityYear: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    buildYear: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    addr: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    phone: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                hProperty: {
                    title:'',
                    name: '',
                    totalPrice:'',
                    refPrice:'',
                    area: '',
                    tradingRight:'',
                    houseType:'',
                    houseOrientation:'',
                    floor:'',
                    decorate:'',
                    equityYear:'',
                    buildYear:'',
                    addr: '',
                    phone: '',
                    status: '',
                    desc: '',
                    isPublic: 1
                },
                dialogImageUrl: '',
                dialogVisible: false,

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                postData : {
                    token: constants.qiniuToken
                },
                images:[],
                imagesList:[]

            }
        },
        methods: {
            formatStatus: function (row, column) {
                var result = "";
                if (row.status == 0) {
                    return ""
                }
                if (row.status == 1) {
                    result = "";
                }
                if (row.status == 2) {
                    result = "";
                }
                if (row.status == 3) {
                    result = "";
                }
                return result;
            },
            handleCurrentChange(val) {
                this.page = val;
                this.gethouse();
            },
            //house
            gethouse() {
                let user = JSON.parse(sessionStorage.getItem('user'))
                let para = {
                    pageNumber: this.page - 1,
                    name: this.filters.name,
                    pageSize:20,
                    userId: user.id
                };
                this.listLoading = true;
                //NProgress.start();
                gethouseListPage(para).then((res) => {
                    this.total = res.data.totalCount;
                    this.house = res.data.data;
                    this.listLoading = false;
                    //NProgress.done();
                });
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {houseId: row.id};
                    deletehouse(para).then((res) => {
                        this.listLoading = false;
                        if(res.data.status == 200) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.gethouse();
                        }

                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.hProperty = Object.assign(this.hProperty, row);
                this.hProperty.price =  this.hProperty.price + "";
                this.hProperty.acreage =  this.hProperty.acreage + "";
                //this.images = this.hProperty.images;
                //console.log(this.hProperty.images);
                this.images = this.hProperty.images;
                this.imagesList = this.hProperty.images.map(function(item){return {"url":item};});
            },
            //
            editSubmit: function () {
                this.$refs.hProperty.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.hProperty);
                            para.images = this.images
                            edithouse(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['hProperty'].resetFields();
                                this.editFormVisible = false;
                                this.gethouse();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { ids: ids };
                    batchRemoveUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.gethouse();
                    });
                }).catch(() => {

                });
            },
            uploadSuccess(res, file, fileList) {
                this.dialogImageUrl = this.qiniuDomain + '/' + res.key;
                this.addImages(this.dialogImageUrl);
            },
            handleRemove(file, fileList) {
                if (file.response == undefined) {
                    this.delImages(file.url);
                } else {
                    this.delImages(this.qiniuDomain + '/' + file.response.key);
                }

            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            addImages(str) {
                this.images.push(str);
            },
            delImages(str) {
                var tmp = this.images;
                this.images.forEach((item, index) => {
                    if (item == str) {
                        tmp.splice(index,1);
                    }
                });
                this.images = tmp;
            },
            init() {
                getDomain({}).then((res) => {
                    this.qiniuDomain = res.data.domain
                });
                getQiniuToken({}).then((res) => {
                    this.qiniuToken = res.data.token
                    this.postData.token = this.qiniuToken;
                });
            }
        },
        mounted() {
            this.gethouse();
            this.init();
        },


    }

</script>

<style scoped>

</style>


